<script>
  import { Line } from 'vue-chartjs'
  export default {
    extends: Line,
    props: [
      'data',
      'labels'
    ],
    data () {
      return {
        datacollection: {
          labels: this.labels,
          datasets: [{
            label: 'Sales (SGD)',
            borderColor: '#b6dfe9',
            pointBackgroundColor: '#b6dfe9',
            borderWidth: 5,
            pointBorderColor: '#b6dfe9',
            backgroundColor: 'transparent',
            data: this.data
          }]
        },
        options: {
          scales: {
            yAxes: [{
              ticks: {
                beginAtZero: false
              },
              gridLines: {
                display: true
              }
            }],
            xAxes: [ {
              gridLines: {
                display: false
              }
            }]
          },
          legend: {
            display: false
          },
          responsive: true,
          maintainAspectRatio: false
        }
      }
    },
    mounted () {
      this.renderChart(this.datacollection, this.options)
    }
  }
</script>


