// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import SuccessAlert from './shared/successAlert.vue'
import ErrorAlert from './shared/errorAlert.vue'
import * as firebase from 'firebase'
import { store } from './vuex/store'

Vue.use(Vuetify, { theme: {
  primary: '#ee44aa',
  secondary: '#424242',
  accent: '#82B1FF',
  error: '#FF5252',
  info: '#2196F3',
  success: '#4CAF50',
  warning: '#FFC107'
}})

Vue.config.productionTip = false

Vue.component('app-success-alert', SuccessAlert)
Vue.component('app-error-alert', ErrorAlert)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  created () {
    // plug in firebase instance
    firebase.initializeApp({
      apiKey: 'AIzaSyBriioi5hm3mN5B5F0uQ1nbF2bZwVVaJdo',
      authDomain: 'blk-party.firebaseapp.com',
      databaseURL: 'https://blk-party.firebaseio.com',
      projectId: 'blk-party',
      storageBucket: 'blk-party.appspot.com',
      messagingSenderId: '57235148521'
    })
    // check for existing user authentication
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoUserLogin', user)
      }
    })
  }
})



