                  open-on-hover
                  :nudge-width="100">
                    <v-toolbar-title slot="activator"
                    class="mt-4 pt-2 ml-2 black--text">
                      <span>{{menuSelected}}</span>
                      <v-icon dark color="black">arrow_drop_down</v-icon>
                    </v-toolbar-title>

                    <v-list>
                      <v-list-tile
                        v-for="category in categories"
                        :key="category"
                        @click=""
                      >
                        <v-list-tile-title v-text="category"></v-list-tile-title>
                      </v-list-tile>
                    </v-list>
                  </v-menu>
                  <v-text-field
                    placeholder="Find your drink"
                    solo
                    color="black"
                    class="mt-4"
                  ></v-text-field>
                  <v-btn 
                  flat 
                  icon>
                    <v-icon class="mt-4 pt-1"
                    color="black">search</v-icon>
                  </v-btn>
                </v-layout>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex d-flex>
            <v-hover>
              <v-card 
              color="blue" 
              dark tile flat
              slot-scope="{ hover }"
              :class="`elevation-${hover ? 12 : 2}`"
              >
                <v-card-text
                class="white--text text-xs-center"
                >
                  {{promo}}
