              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex d-flex xs12 sm3>
        <v-card color="black" 
        dark 
        tile 
        flat>
          <v-card-text
          class="display-5 text-xs-center">
            Rent This Ad Space
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout 
    row wrap
    class="my-2">
      <v-carousel 
      hide-delimiters
      class="carousel"
      >
        <v-carousel-item
          v-for="(deal,i) in deals"
          :key="i"
          :src="require('@/assets/' + deal.img + '.jpg')"
          reverse-transition="fade"
          transition="fade"         
        >
        </v-carousel-item>
      </v-carousel>
    </v-layout>
    <v-layout row wrap
    justify-space-around
    class="mt-4">
      <v-flex xs12 sm3>
        <v-hover>
          <v-card
          slot-scope="{ hover }"
          :class="`elevation-${hover ? 12 : 2}`">
            <v-layout>
              <v-flex xs12>
                <v-img
                  src="http://www.kohajone.com/wp-content/uploads/2018/08/drinks.jpg"
                  height="200px"
                  contain
                ></v-img>
              </v-flex>
            </v-layout>
            <v-card-title primary-title>
              <div>
                <div class="headline">Party Essentials</div>
              </div>
            </v-card-title>
            <v-card-actions class="pa-3">
              <v-btn 
              flat
              class="orange--text"
              :to="'/shop'"
              >Shop All</v-btn>
            </v-card-actions>
          </v-card>
        </v-hover>
      </v-flex>
      <v-flex xs12 sm3>
        <v-hover>
          <v-card
          slot-scope="{ hover }"
          :class="`elevation-${hover ? 12 : 2}`">
            <v-layout>
              <v-flex xs12>
                <v-img
                  src="http://www.redparkapub.com/wp-content/uploads/2015/06/g.jpg"
                  height="200px"
                  contain
                ></v-img>
              </v-flex>
            </v-layout>
            <v-card-title primary-title>
              <div>
                <div class="headline">Beach Party Picks</div>
              </div>
            </v-card-title>
            <v-card-actions class="pa-3">
              <v-btn 
              flat
              class="orange--text"
              :to="'/shop'"
              >See Collection</v-btn>
            </v-card-actions>
          </v-card>
        </v-hover>
      </v-flex>
      <v-flex xs12 sm3>
        <v-hover>
          <v-card
          slot-scope="{ hover }"
          :class="`elevation-${hover ? 12 : 2}`">
            <v-layout>
              <v-flex xs12>
                <v-img
                  src="https://i1.wp.com/www.spiritedsingapore.com/wp-content/uploads/2015/08/Sam-Wong-1-copy.jpg?fit=900%2C600&ssl=1"
