                  contain
                ></v-img>
              </v-flex>
            </v-layout>
            <v-card-title primary-title>
              <div>
                <div class="headline">Store of the Week</div>
              </div>
            </v-card-title>
            <v-card-actions class="pa-3">
              <v-btn 
              flat
              class="orange--text"
              :to="'/shop'"
              >Learn More</v-btn>
            </v-card-actions>
          </v-card>
        </v-hover>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data: () => ({
      menuSelected: 'All',
      promo: 'This week only: Get 20% off all whisky! Type WHISKY20 on checkout.',
      categories: [
        'Beer', 'Whisky', 'Vodka', 'Gin', 'Rum', 'Tequila', 'Brandy', 'Wine', 'Champagne'
      ],
      deals: [
        {
          name: 'Beer',
          img: 'beer'
        },
        {
          name: 'Rum',
          img: 'rum'
        },
        {
          name: 'Vodka',
          img: 'vodka'
        }
      ]
    })
  }
</script>

<style scoped>
  .carousel {
    height: 450px;
    width: 100%;
    cursor: pointer;
  }
</style>



