export default {
  getVendorOrders (state) {
    return state.orders.sort((orderA, orderB) => {
      return orderA.dateOrdered > orderB.dateOrdered
    })
  },
  getVendorOrder (state) {
    return (orderId) => {
      return state.orders.find((order) => {
        return order.id === orderId
      })
    }
  },
  getUpdatedItems: state => state.items,
  getVendorItems (state) {
    return vendorId => state.items.filter(item => {
      return item.vendorId === vendorId
    })
  },
  getItem (state) {
    return (itemId) => {
      return state.items.find((item) => {
        return item.id === itemId
      })
    }
  },
  getOverall (state) {
    return state.overall
  },
  getSales (state) {
    return state.sales
  },
  getLabels (state) {
    return state.labels
  },
  getVendor (state) {
    return state.vendor
  },
  getUser (state) {
    return state.user
  },
  getLoading (state) {
    return state.loading
  },
  getError (state) {
    return state.error
  },
  getSuccess (state) {
    return state.success
  }
}



