import * as firebase from 'firebase'

export default {
  // action to get all items
  itemsApi ({commit}) {
    commit('SET_LOADING', true)
    firebase.database().ref('items').child('booze').once('value')
    .then(data => {
      const items = []
      const obj = data.val()
      for (let key in obj) {
        items.push({
          itemId: obj[key].itemId,
          itemName: obj[key].itemName,
          description: obj[key].description,
          priceSGD: obj[key].priceSGD,
          discount: obj[key].discount,
          vendorId: obj[key].vendorId,
          createDate: obj[key].createDate,
          listPeriod: obj[key].listPeriod,
          itemType: obj[key].itemType,
          imageURL: obj[key].imageURL
        })
      }
      commit('SET_ITEMS', items)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // action to get oveall performance
  overallApi ({commit}, payload) {
    commit('SET_LOADING', true)
    const vendorId = payload.id
    firebase.database().ref('users').child(vendorId).child('overall').once('value')
    .then(data => {
      const overall = {}
      const obj = data.val()
      overall.valueSGD = obj.valueSGD
      overall.change = obj.change
      commit('SET_OVERALL', overall)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to get vendor orders
  vendorOrdersApi ({commit}, payload) {
    commit('SET_LOADING', true)
    const vendorId = payload.id
    firebase.database().ref('users').child(vendorId).child('orders').once('value')
    .then(data => {
      const orders = []
      const obj = data.val()
      for (let key in obj) {
        orders.push({
          orderId: obj[key].orderId,
          itemId: obj[key].itemId,
          itemName: obj[key].itemName,
          vendorId: obj[key].vendorId,
          userId: obj[key].userId,
          userName: obj[key].userName,
          fee: obj[key].fee,
          discount: obj[key].discount,
          destination: obj[key].destination,
          dateOrdered: obj[key].dateOrdered,
          dateDelivered: obj[key].dateDelivered,
          priceSGD: obj[key].priceSGD,
          amountPaid: obj[key].amountPaid,
          userCheck: obj[key].userCheck,
          quantity: obj[key].quantity,
          netSales: obj[key].netSales
        })
      }
      commit('SET_ORDERS', orders)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to get performance chart data
  chartApi ({commit}, payload) {
    commit('SET_LOADING', true)
    const vendorId = payload.id
    firebase.database().ref('users').child(vendorId).child('performance').orderByKey().once('value')
    .then(d => {
      const chartData = []
      const chartLabels = []
      const obj = d.val()
      // push data and labels into separate arrays
      for (let key in obj) {
        chartData.push({
          index: key,
          valueSGD: obj[key].valueSGD
        })
        chartLabels.push({
          index: key,
          date: obj[key].date
        })
      }
      // sort data and label arrays by index
      const sales = chartData.reduce(function (d, e) {
        d[e.index] = e.valueSGD
        return d
      }, [])
      const labels = chartLabels.reduce(function (l, e) {
        l[e.index] = e.date
        return l
      }, [])
      commit('SET_SALES', sales)
      commit('SET_LABELS', labels)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // user login actions
  userLogin ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const userId = data.user.uid
      firebase.database().ref('users').child(userId).child('profile').once('value')
      .then(profile => {
        const obj = profile.val()
        if (obj.type === 'user') {
          const newUser = {
            id: userId,
            email: payload.email,
            name: obj.name,
            dateJoined: obj.dateJoined,
            avatar: obj.avatar,
            type: obj.type
          }
          commit('SET_USER', newUser)
          commit('SET_VENDOR', null)
          commit('SET_LOADING', false)
        } else {
          commit('SET_VENDOR', null)
          commit('SET_LOADING', false)
          commit('SET_ERROR', 'User profile missing. Please login as vendor or register as a new user.')
        }
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // vendor login actions
  vendorLogin ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const vendorId = data.user.uid
      firebase.database().ref('users').child(vendorId).child('profile').once('value')
      .then(profile => {
        const obj = profile.val()
        if (obj.type === 'vendor') {
          const newVendor = {
            id: vendorId,
            email: payload.email,
            name: obj.name,
            dateJoined: obj.dateJoined,
            avatar: obj.avatar,
            type: obj.type
          }
          commit('SET_VENDOR', newVendor)
          commit('SET_USER', null)
          commit('SET_LOADING', false)
        } else {
          commit('SET_USER', null)
          commit('SET_LOADING', false)
          commit('SET_ERROR', 'Vendor profile missing. Please login as user or register as a new vendor.')
        }
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // auto login functions
  autoUserLogin ({commit}, payload) {
    const loginId = payload.uid
    firebase.database().ref('users').child(loginId).child('profile').once('value')
    .then(profile => {
      const obj = profile.val()
      if (obj.type === 'user') {
        const user = {
          id: loginId,
          email: payload.email,
          name: obj.name,
          dateJoined: obj.dateJoined,
          avatar: obj.avatar,
          type: obj.type
        }
        commit('SET_USER', user)
        commit('SET_LOADING', false)
      } else {
        const vendor = {
          id: loginId,
          email: payload.email,
          name: obj.name,
          dateJoined: obj.dateJoined,
          avatar: obj.avatar,
          type: obj.type
        }
        commit('SET_VENDOR', vendor)
        commit('SET_LOADING', false)
      }
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // user register actions
  userRegister ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const newUser = {
        email: payload.email,
        name: payload.name,
        avatar: payload.avatar,
        dateJoined: payload.dateJoined.toISOString(),
        type: 'user'
      }
      const userId = data.user.uid
      // create vendor child with userId in firebase DB
      return firebase.database().ref('users').child(userId).child('profile').set(newUser)
      .then(() => {
        commit('SET_USER', {
          ...newUser,
          id: userId
        })
        commit('SET_LOADING', false)
      })
      .catch(error => {
        commit('SET_LOADING', false)
        commit('SET_ERROR', error.message)
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // vendor register actions
  vendorRegister ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const newVendor = {
        email: payload.email,
        name: payload.name,
        avatar: payload.avatar,
        dateJoined: payload.dateJoined.toISOString(),
        type: 'vendor'
      }
      const overall = {
        change: 0,
        valueSGD: 0
      }
      const vendorId = data.user.uid
      // create vendor child with userId in firebase DB
      return firebase.database().ref('users').child(vendorId).child('profile').set(newVendor)
      .then(() => {
        return firebase.database().ref('users').child(vendorId).child('overall').set(overall)
        .then(() => {
          commit('SET_VENDOR', {
            ...newVendor,
            id: vendorId
          })
          commit('SET_LOADING', false)
        })
        .catch(error => {
          commit('SET_LOADING', false)
          commit('SET_ERROR', error.message)
        })
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // action to upload files
  uploadFile ({commit}, payload) {
    const file = {
      itemName: payload.itemName,
      priceSGD: payload.priceSGD,
      description: payload.description,
      itemType: payload.itemType,
      discount: payload.discount,
      listPeriod: payload.listPeriod,
      vendorId: payload.vendorId,
      createDate: payload.createDate.toISOString()
    }
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    commit('CLEAR_SUCCESS')
    let key
    // create file key first in firebase DB
    firebase.database().ref('items').child('booze').push(file)
    .then((data) => {
      key = data.key
      return key
    })
    // store image in firebase storage
    .then(key => {
      return firebase.storage().ref('files/' + 'f' + key).put(payload.file)
    })
    // retrieve image URL to store as file property in firebase DB
    .then(fileData => {
      fileData.ref.getDownloadURL()
      .then(url => {
        return firebase.database().ref('items').child('booze').child(key).update({
          imageURL: url,
          itemId: key
        })
      })
      commit('SET_LOADING', false)
      commit('SET_SUCCESS', 'All item(s) uploaded!')
    })
    // store error message
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to update selected file
  updateFile ({commit}, payload) {
    const item = {
      itemId: payload.itemId,
      itemName: payload.itemName,
      priceSGD: payload.priceSGD,
      description: payload.description,
      itemType: payload.itemType,
      discount: payload.discount,
      listPeriod: payload.listPeriod,
      vendorId: payload.vendorId,
      createDate: payload.createDate,
      updateDate: payload.updateDate.toISOString()
    }
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    commit('CLEAR_SUCCESS')
    let itemId = payload.itemId
    // find by itemId and update item
    firebase.database().ref('items').child('booze').child(itemId).set(item)
    // if no updatedFile, no change to imageURL
    if (payload.updatedFile === null) {
      firebase.database().ref('items').child('booze').child(itemId).update({
        imageURL: payload.imageURL
      })
      .then(() => {
        commit('SET_LOADING', false)
        commit('SET_SUCCESS', 'Item updated!')
      })
      // store error message
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
    } else {
      // store new image in firebase storage
      firebase.storage().ref('files/' + 'f' + itemId).put(payload.updatedFile)
      // retrieve image URL and update item imageURL
      .then(fileData => {
        fileData.ref.getDownloadURL()
        .then(url => {
          return firebase.database().ref('items').child('booze').child(itemId).update({
            imageURL: url
          })
        })
        commit('SET_LOADING', false)
        commit('SET_SUCCESS', 'Item updated!')
      })
      // store error message
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
    }
  },
  // action to delete one item
  deleteItem ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    // remove item from firebase
    firebase.database().ref('items').child('booze').child(payload.itemId).remove()
    .then(() => {
      commit('SET_LOADING', false)
      commit('SET_SUCCESS', 'Item successfully deleted')
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  logout ({commit}) {
    firebase.auth().signOut()
    commit('SET_USER', null)
    commit('SET_VENDOR', null)
    commit('SET_ORDERS', null)
    commit('SET_OVERALL', null)
    commit('SET_ITEMS', null)
    commit('SET_SALES', null)
    commit('SET_LABELS', null)
    commit('CLEAR_ERROR')
  },
  setSuccess ({commit}, payload) {
    commit('SET_SUCCESS', payload.message)
  },
  setError ({commit}, payload) {
    commit('SET_ERROR', payload.message)
  },
  clearSuccess ({commit}) {
    commit('CLEAR_SUCCESS')
  },
  clearError ({commit}) {
    commit('CLEAR_ERROR')
  }
}



