<template>
  <v-app>
    <v-navigation-drawer
      v-model="drawer"
      enable-resize-watcher
      app
    >
      <v-list>
        <p
        v-if="user"
        class="ml-3 my-2">
          Welcome {{user.name}}!
        </p>
        <p
        v-if="vendor"
        class="ml-3 my-2">
          Welcome {{vendor.name}}!
        </p>
        <v-list-tile
        v-for="item in menuItems"
        :key="item.title"
        :to="item.link"
        @click="onDismissed"        
        class="dark--text"
        active-class="success--text"
        >
          <v-list-tile-action>
            <v-icon left>{{item.icon}}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title> {{item.title}}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile
        :to="'/cart'"
        @click="onDismissed"
        active-class="success--text"
        >
          <v-list-tile-action>
            <v-badge
              left 
              overlap
              color="warning"
            >
              <span slot="badge" black--text>{{cartCount}}</span>
              <v-icon>shopping_cart</v-icon>
            </v-badge>
          </v-list-tile-action>
          <v-list-tile-content>Your Cart</v-list-tile-content>
        </v-list-tile>
        <v-list-tile
        v-if="userAuthenticated"
        @click="onLogout"
        :to="'/'">
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>Logout</v-list-tile-content>
        </v-list-tile>
        <v-list-tile
        v-if="vendorAuthenticated"
        @click="onLogout"
        :to="'/'">
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>Logout</v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      app
      fixed
      class="dark--text elevation-0"
      id="toolbar"
    >
      <v-toolbar-side-icon 
      class="hidden-md-and-up"
      @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link
        to="/" 
        @click="onDismissed"        
        tag="span" 
        style="cursor: pointer">
          <img src="@/assets/logo.png" style="height: 55px">
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <p
      v-if="user"
      class="ml-3 my-2 mr-2">
        Welcome {{user.name}}! 
      </p>
      <p
      v-if="vendor"
      class="ml-3 my-2 mr-2">
        Welcome {{vendor.name}}! 
      </p>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn 
        flat 
        v-for="item in menuItems" 
        :key="item.title"
        :to="item.link"
        @click="onDismissed"        
        class="dark--text"
        >
          <v-icon left>{{ item.icon }}</v-icon>
          {{ item.title }}
        </v-btn>
        <v-btn
        :to="'/cart'"
        @click="onDismissed"        
        flat>
          <v-badge
            left 
            overlap
            color="warning"
          >
            <span slot="badge" black--text>{{cartCount}}</span>
            <v-icon left>shopping_cart</v-icon>
          </v-badge>
          Your Cart
        </v-btn>
        <v-btn
        v-if="userAuthenticated"
        @click="onLogout"
        :to="'/'"        
        flat>
          <v-icon left>exit_to_app</v-icon>
          Logout
        </v-btn>
        <v-btn
        v-if="vendorAuthenticated"
        @click="onLogout"
        :to="'/'"        
        flat>
          <v-icon left>exit_to_app</v-icon>
          Logout
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-content class="mt-4 pt-3">
      <router-view/>
    </v-content>
    <v-footer class="ml-3 transparent" app>
      <span>&copy; Blk.Party 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  data () {
    return {
      drawer: false,
      cartCount: 0
    }
  },
  // guard nav items according to user login status
  computed: {
    menuItems () {
      let menuItems = [
        {
          icon: 'local_bar',
          title: 'Find Your Drink',
          link: '/shop'
        },
        {
          icon: 'info',
          title: 'About Blk.Party',
          link: '/about'
        },
        {
          icon: 'lock_open',
          title: 'Login/Register',
          link: '/login'
        }
      ]
      if (this.userAuthenticated) {
        menuItems = [
          {
            icon: 'local_bar',
            title: 'Find Your Drink',
            link: '/shop'
          }
        ]
      }
      if (this.vendorAuthenticated) {
        menuItems = [
          {
            icon: 'insert_chart', title: 'Dashboard', link: '/dashboard'
          },
          {
            icon: 'local_bar',
            title: 'Find Your Drink',
            link: '/shop'
          }
        ]
      }
      return menuItems
    },
    userAuthenticated () {
      return this.$store.getters.getUser !== null && this.$store.getters.getUser !== undefined
    },
    user () {
      return this.$store.getters.getUser
    },
    vendorAuthenticated () {
      return this.$store.getters.getVendor !== null && this.$store.getters.getVendor !== undefined
    },
    vendor () {
      return this.$store.getters.getVendor
    }
  },
  methods: {
    changeRoute (a) {
      this.$router.push({ path: a.route })
    },
    onLogout () {
      this.$store.dispatch('logout')
    },
    onDismissed () {
      this.$store.dispatch('clearSuccess')
      this.$store.dispatch('clearError')
    }
  },
  name: 'App'
}
</script>

<style scoped>
  #toolbar {
    opacity: 0.9;
  }
  .title {
    border: 1px solid black
  }
</style>




