import Vue from 'vue'
import Vuex from 'vuex'

import mutations from './mutations'
import actions from './actions'
import getters from './getters'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    orders: [
      {
        orderId: 'Loading...',
        itemId: 'Loading...',
        itemName: 'Loading...',
        vendorId: 'Loading...',
        userId: 'Loading...',
        userName: 'Loading...',
        fee: 'Loading...',
        discount: 'Loading...',
        destination: 'Loading...',
        dateOrdered: 'Loading...',
        dateDelivered: 'Loading...',
        priceSGD: 'Loading...',
        amountPaid: 'Loading...',
        userCheck: 'Loading...',
        quantity: 'Loading...',
        netSales: 'Loading...'
      }
    ],
    items: [],
    sales: [{valueSGD: 'Loading...'}],
    labels: [{date: 'Loading...'}],
    overall: [
      {
        valueSGD: 'Loading',
        change: 'Loading'
      }],
    vendor: null,
    user: null,
    error: null,
    success: null,
    loading: false
  },
  mutations,
  actions,
  getters
})



