<template>
  <v-container 
  fluid
  fill-height>
    <v-layout 
    row wrap
    justify-center
    align-center>
      <v-card 
      color="grey lighten-4">
        <v-toolbar 
        color="grey lighten-1">
          <v-toolbar-title>{{loginStatus}} as:</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn 
            color="grey darken-3" 
            dark
            flat
            @click="switchVendorLogin">
              <v-icon left>supervisor_account</v-icon>
              Vendor
            </v-btn>
            <v-btn 
            color="grey darken-3"
            flat
            @click="switchUserLogin">
              <v-icon left>shopping_basket</v-icon>
              User
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-layout 
        row
        v-if="error">
          <v-flex xs12>
            <app-error-alert 
            @dismissed="onDismissed"
            :text="error"
            class="black--text"></app-error-alert>
          </v-flex>
        </v-layout>
        <v-card-text>
          <v-container>

            <!-- vendor login form -->
            <form 
            v-if="vendorLogin"
            @submit.prevent="onVendorLogin">
              <v-layout row>
                <v-flex xs12>
                  <h2>Vendor Login</h2>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  name="email"
                  label="Email"
                  id="email"
                  v-model="email"
                  type="email"
                  color="blue"
                  :rules="[rules.required, rules.email]"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  v-model="password"
                  color="blue" 
                  :append-icon="showPassword ? 'visibility_off' : 'visibility'"
                  :type="showPassword ? 'text' : 'password'"
                  name="password"
                  label="Password"
                  @click:append="showPassword = !showPassword"
                  :rules="[rules.required]"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-btn 
                  type="submit"
                  color="blue"
                  dark                  
                  :disabled="loading"
                  :loading="loading"
                  >Log In
                  <span slot="loader" class="custom-loader">
                    <v-icon light>cached</v-icon>
                  </span>
                  </v-btn>
                  <v-btn
                  flat small color="blue"
                  dark
                  @click="switchVendorRegister">
                    New vendor? Register here
                  </v-btn>
                </v-flex>
              </v-layout>
            </form>

            <!-- vendor registration form -->
            <form 
            v-if="vendorRegister"
            @submit.prevent="onVendorRegister">
              <v-layout row>
                <v-flex xs12>
                  <h2>New Vendor Register</h2>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  name="name"
                  label="Your Name"
                  id="name"
                  v-model="name"
                  type="text"
                  color="blue"
                  :rules="[rules.required, rules.counter]"
                  counter
                  maxlength="20">
                  </v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  name="email"
                  label="Email"
                  id="email"
                  v-model="email"
                  type="email"
                  color="blue"
                  :rules="[rules.required, rules.email]"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  v-model="password"
                  color="blue" 
                  :append-icon="showPassword ? 'visibility_off' : 'visibility'"
                  :type="showPassword ? 'text' : 'password'"
                  name="password"
                  label="Password"
                  counter
                  @click:append="showPassword = !showPassword"
                  :rules="[rules.required, rules.password]"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  name="retype-password"
                  label="Retype Password"
                  id="retypepassword"
                  v-model="retypePassword"
                  type="password"
                  color="blue" 
                  required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-btn 
                  type="submit"
                  class="blue"
                  dark                  
                  :disabled="loading"
                  :loading="loading"
                  >Register
                  <span slot="loader" class="custom-loader">
                    <v-icon light>cached</v-icon>
                  </span>
                  </v-btn>
                  <v-btn
                  flat small color="blue"
                  dark
                  @click="switchVendorLogin">
                    Already a vendor? Login here
                  </v-btn>
                </v-flex>
              </v-layout>
            </form>

            <!-- user login form -->
            <form 
            v-if="userLogin"
            @submit.prevent="onUserLogin">
              <v-layout row>
                <v-flex xs12>
                  <h2>User Login</h2>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  name="email"
                  label="Email"
                  id="email"
                  v-model="email"
                  type="email"
                  color="blue"
                  :rules="[rules.required, rules.email]"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  v-model="password"
                  color="blue" 
                  :append-icon="showPassword ? 'visibility_off' : 'visibility'"
                  :type="showPassword ? 'text' : 'password'"
                  name="password"
                  label="Password"
                  @click:append="showPassword = !showPassword"
                  :rules="[rules.required]"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-btn 
                  type="submit"
                  class="blue"
                  dark                  
                  :disabled="loading"
                  :loading="loading"
                  >Log In
                  <span slot="loader" class="custom-loader">
                    <v-icon light>cached</v-icon>
                  </span>
                  </v-btn>
                  <v-btn
                  flat small color="blue"
                  @click="switchUserRegister">
                    New to Blk.Party? Register here
                  </v-btn>
                </v-flex>
              </v-layout>
            </form>

            <!-- user registration form -->
            <form 
            v-if="userRegister"
            @submit.prevent="onUserRegister">
              <v-layout row>
                <v-flex xs12>
                  <h2>New User Register</h2>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  name="name"
                  label="Your Name"
                  id="name"
                  v-model="name"
                  type="text"
                  color="blue"
                  :rules="[rules.required, rules.counter]"
                  counter
                  maxlength="20">
                  </v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  name="email"
                  label="Email"
                  id="email"
                  v-model="email"
                  type="email"
                  color="blue"
                  :rules="[rules.required, rules.email]"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  v-model="password"
                  color="blue" 
                  :append-icon="showPassword ? 'visibility_off' : 'visibility'"
                  :type="showPassword ? 'text' : 'password'"
                  name="password"
                  label="Password"
                  counter
                  @click:append="showPassword = !showPassword"
                  :rules="[rules.required, rules.password]"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                  name="retype-password"
                  label="Retype Password"
                  id="retypepassword"
                  v-model="retypePassword"
                  type="password"
                  color="blue" 
                  required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-btn 
                  type="submit"
                  class="blue"
                  dark
                  :disabled="loading"
                  :loading="loading"
                  >Register
                  <span slot="loader" class="custom-loader">
                    <v-icon light>cached</v-icon>
                  </span>
                  </v-btn>
                  <v-btn
                  flat small color="blue"
                  @click="switchUserLogin">
                    Already a Blk.Party user? Login here
                  </v-btn>
                </v-flex>
              </v-layout>
            </form>
          </v-container>
        </v-card-text>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      avatar: '',
      email: '',
      password: '',
      retypePassword: '',
      showPassword: false,
      loginStatus: 'Login',
      userLogin: true,
      userRegister: false,
      vendorLogin: false,
      vendorRegister: false,
      loader: null,
      rules: {
        required: value => !!value || 'Required info.',
        counter: value => value.length <= 20 || 'Max 20 characters',
        password: v => v.length >= 7 || 'Min 7 characters',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Invalid e-mail.'
        }
      }
    }
  },
  computed: {
    user () {
      return this.$store.getters.getUser
    },
    vendor () {
      return this.$store.getters.getVendor
    },
    error () {
      return this.$store.getters.getError
    },
    loading () {
      return this.$store.getters.getLoading
    }
  },
  watch: {
    // listen to user login states
    user (value) {
      if (
        value.type === 'user'
      ) {
        this.$router.push('/shop')
      }
    },
    // listen to vendor login states
    vendor (value) {
      if (
        value.type === 'vendor'
      ) {
        this.$router.push('/dashboard')
      }
    }
  },
  methods: {
    switchUserLogin () {
      this.userLogin = true
      this.userRegister = false
      this.vendorLogin = false
      this.vendorRegister = false
      this.loginStatus = 'Login'
    },
    switchUserRegister () {
      this.userLogin = false
      this.userRegister = true
      this.vendorLogin = false
      this.vendorRegister = false
      this.loginStatus = 'Register'
    },
    switchVendorLogin () {
      this.userLogin = false
      this.userRegister = false
      this.vendorLogin = true
      this.vendorRegister = false
      this.loginStatus = 'Login'
    },
    switchVendorRegister () {
      this.userLogin = false
      this.userRegister = false
      this.vendorLogin = false
      this.vendorRegister = true
      this.loginStatus = 'Register'
    },
    onUserLogin () {
      this.$store.dispatch('userLogin', {
        email: this.email,
        password: this.password
      })
    },
    onUserRegister () {
      if (this.password === this.retypePassword) {
        this.$store.dispatch('clearError')
        this.$store.dispatch('userRegister', {
          email: this.email,
          password: this.password,
          name: this.name,
          avatar: this.avatar,
          dateJoined: new Date()
        })
      } else {
        this.$store.dispatch('setError', {message: 'Passwords do not match. Please retype password.'})
      }
    },
    onVendorLogin () {
      this.$store.dispatch('vendorLogin', {
        email: this.email,
        password: this.password
      })
    },
    onVendorRegister () {
      if (this.password === this.retypePassword) {
        this.$store.dispatch('clearError')
        this.$store.dispatch('vendorRegister', {
          email: this.email,
          password: this.password,
          name: this.name,
          avatar: this.avatar,
          dateJoined: new Date()
        })
      } else {
        this.$store.dispatch('setError', {message: 'Passwords do not match. Please retype password.'})
      }
    },
    onDismissed () {
      this.$store.dispatch('clearError')
    }
  }
}
</script>

<style scoped>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>




