<template>
  <v-container grid-list-md fluid>

    <!-- photo list -->
    <v-layout 
    row wrap 
    v-if="loading">
      <v-flex xs12 class="text-xs-center mt-5">
        Getting your drinks... 
        <v-progress-circular 
        indeterminate 
        color="green"
        :width="3"
        :size="20"
        ></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout 
    row wrap
    v-if="!itemsLoaded">
      <h3>No drinks to display yet.</h3>
    </v-layout>
    <v-layout 
    row wrap 
    v-if="itemsLoaded"
    class="mt-3">
      <v-flex
        v-for="item in  items"
        :key="item.id"
        xs12 sm4
      >
        <v-hover>
          <v-card
          slot-scope="{ hover }"
          :class="`elevation-${hover ? 12 : 2}`">
            {{item.id}}
            <v-img
              :src="item.imageURL"
              height="250px"
              contain
              style="cursor: pointer"
              @click="openDialog(item)"
            >
            </v-img>

            <v-card-title primary-title>
              <div>
                <div class="headline">{{item.itemName}}</div>
                <div>SGD {{item.priceSGD}} each</div>
              </div>
            </v-card-title>

            <v-card-actions>
              <v-chip color="warning" text-color="white">
                {{item.itemType}}
              </v-chip>
              <v-spacer></v-spacer>
              
              <!-- likeItem buttons -->
              <v-tooltip
              bottom>
                <v-btn 
                slot="activator"
                icon 
                v-if="likedItem(item) === false"
                @click="like(item)">
                  <v-icon color="error">favorite_border</v-icon>
                </v-btn>
                <span>Add to Wishlist</span>
              </v-tooltip>
              <v-tooltip
              bottom>
                <v-btn 
                slot="activator"
                icon 
                v-if="!likedItem(item) === true"
                @click="unlike(item)">
                  <v-icon color="error">favorite</v-icon>
                </v-btn>
                <span>Remove from Wishlist</span>
              </v-tooltip>

              <!-- add shopping cart buttons -->
              <v-tooltip
              bottom>
                <v-btn 
                slot="activator"
                icon 
                v-if="addCart(item) === false"
                @click="shop(item)">
                  <v-icon color="success">add_shopping_cart</v-icon>
                </v-btn>
                <span>Add to Cart</span>
              </v-tooltip>
              <v-tooltip
              bottom>
                <v-btn 
                slot="activator"
                icon 
                v-if="!addCart(item) === true"
                @click="unshop(item)">
                  <v-icon color="success">shopping_cart</v-icon>
                </v-btn>
                <span>Remove from Cart</span>
              </v-tooltip>

              <!-- view item button 
              <v-tooltip bottom>
                <v-btn 
                slot="activator"
                flat
                icon
                color="primary"
                @click="openDialog(item)">
                  <v-icon>all_out</v-icon>
                </v-btn>
                <span>View Item</span>
              </v-tooltip -->
            </v-card-actions>
          </v-card>
        </v-hover>
      </v-flex>
    </v-layout>

    <!-- item dialog
    <v-dialog
    transition="dialog-bottom-transition"
    height="700px"
    v-model="itemDialog">
      <v-card>
        <v-btn 
        icon @click="closeDialog">
          <v-icon>close</v-icon>
        </v-btn>
        <v-layout 
          row
          v-if="error"
          class="mb-2">
            <v-flex xs12>
              <app-error-alert 
              @dismissed="onDismissedError"
              :text="error.message"></app-error-alert>
            </v-flex>
          </v-layout>
          <v-layout 
          row
          v-if="message"
          class="mb-2">
            <v-flex xs12>
              <app-success-alert 
              @dismissed="onDismissedMessage"
              :text="message"></app-success-alert>
            </v-flex>
          </v-layout>
        <v-layout row wrap>
          <v-flex xs12 sm8>
            <v-card-media
            :src="imageURL"
            height="400px"
            contain
          >
            </v-card-media>
          </v-flex>
          <v-flex xs12 sm4>
            <v-card-title>
              <div class="headline">
                {{initialComment}}
              </div>
              <br>
              <div>
                Uploaded by {{creator}}, on {{createDate | dateFilter}}   
              </div>
            </v-card-title>
            <v-divider></v-divider>
            <v-layout row wrap>
              <v-card-text>
                <v-layout v-if="noComments"
                row wrap>
                  <h4>{{noCommentsMsg}}</h4>
                </v-layout>
                <v-layout v-if="loading">
                  <p>Uploading comment ... 
                    <v-progress-circular
                    indeterminate 
                    color="primary"
                    :width="3"
                    :size="15"
                    ></v-progress-circular>
                  </p>
                </v-layout>
                <v-layout row wrap
                v-if="!loading">
                  <v-flex xs12 sm7>
                    <v-text-field 
                    v-model="newComment"
                    name="new comment"
                    color="primary"
                    label="Your Comment">
                    </v-text-field>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <v-tooltip bottom>
                      <v-btn 
                      slot="activator"
                      icon
                      fab
                      small
                      outline
                      color="error"
                      @click="clearComment">
                        <v-icon>delete_outline</v-icon>
                      </v-btn>
                      <span>Clear comment</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                      <v-btn 
                      slot="activator"
                      icon
                      fab
                      small
                      outline
                      color="success"
                      @click="saveEdits">
                        <v-icon>check</v-icon>
                      </v-btn>
                      <span>Add comment</span>
                    </v-tooltip>
                  </v-flex>
                </v-layout>
                <div class="commentList">
                  <v-layout
                  v-if="!loading"
                  row wrap
                  v-for="(comment, key) in comments"
                  :key="key"
                  class="my-4">
                    <div>
                      <div class="subheading">
                        {{comment.message}}
                      </div>
                      <div>
                        By: {{comment.creator}}
                      </div>
                    </div>
                  </v-layout>
                </div>
              </v-card-text>
            </v-layout>
          </v-flex>
        </v-layout>
        <v-layout 
        align-end
        justify-center
        fill-height
        row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>     
              <v-btn 
              flat 
              class="blue--text darken-1"
              @click="closeDialog">Close</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-card>
    </v-dialog -->
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      // itemDialog: false,
      imageURL: null
      // creator: null,
      // createDate: null,
      // initialComment: '',
      // newComment: '',
      // noCommentsMsg: '',
      // comments: []
    }
  },
  mounted () {
    this.init()
  },
  computed: {
    ...mapGetters({
      items: 'getUpdatedItems'
    }),
    itemsLoaded () {
      return this.items != null || undefined
    },
    user () {
      return this.$store.getters.getUser
    },
    vendor () {
      return this.$store.getters.getVendor
    },
    error () {
      return this.$store.getters.getError
    },
    success () {
      return this.$store.getters.getSuccess
    },
    loading () {
      return this.$store.getters.getLoading
    }
  },
  methods: {
    // initialization function to be called on mounted lifecycle hook
    init () {
      // load all photo files
      this.$store.dispatch('itemsApi')
    },
    // dismiss alerts
    onDismissedError () {
      this.$store.dispatch('clearError')
    },
    onDismissedMessage () {
      this.$store.dispatch('clearMessage')
    },
    // item dialog methods
    openDialog (item) {
      // open itemDialog
      this.itemDialog = true
      // set dialog params
      this.imageURL = item.imageURL
    },
    closeDialog () {
      // close itemDialog
      this.itemDialog = false
    },
    // to-dos
    likedItem (item) {
    },
    addCart (item) {
    }
  }
}
</script>




