                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                        class="error"
                        @click="deleteItem"
                        >Confirm Delete</v-btn>
                      </v-card-actions>
                    </v-flex>
                  </v-layout>
                </v-card>
              </v-dialog>
            </v-container>

          </v-layout>
        </v-card>
      </v-flex>

      <!-- add item FAB -->
      <v-btn
        class="hidden-sm-and-down"
        v-if="!addItem"
        color="error"
        dark
        absolute
        right
        fab
        @click="showAddItem"
      >
        <v-icon>add</v-icon>
      </v-btn>

      <!-- dashboard nav speed dial -->
      <v-speed-dial
        class="hidden-md-and-up"
        v-model="fab"
        absolute
        right
        direction="bottom"
        open-on-hover
        transition="slide-y-reverse-transition"
      >
        <v-btn
          slot="activator"
          v-model="fab"
          color="blue darken-2"
          dark
          fab
        >
          <v-icon>account_circle</v-icon>
          <v-icon>close</v-icon>
        </v-btn>
        <v-btn
          fab
          dark
          small
          color="error"
          @click="showAddItem"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-btn
          fab
          dark
          small
          color="success"
          @click="showUpdateItem"
        >
          <v-icon>edit</v-icon>
        </v-btn>
        <v-btn
          fab
          dark
          small
          color="warning"
          @click="showOverview"
        >
          <v-icon>assignment</v-icon>
        </v-btn>
        <v-btn
          fab
          dark
          small
          color="accent"
          @click="showPies"
        >
          <v-icon>data_usage</v-icon>
        </v-btn>
        <v-btn
          fab
          dark
          small
          color="primary"
          @click="showProfile"
        >
          <v-icon>settings</v-icon>
        </v-btn>
      </v-speed-dial>

      <!-- terms and conditions dialogs -->
      <v-dialog v-model="showTerms" width="70%">
        <v-card>
          <v-card-title 
          class="headline grey lighten-2"
          primary-title
          >Vendor Terms</v-card-title>
          <v-card-text v-for="n in 5" :key="n">
            {{ content }}
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              flat
              color="success"
              @click="showTerms = false"
            >Understood</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="showConditions" width="70%">
        <v-card>
          <v-card-title 
          class="headline grey lighten-2"
          primary-title
          >Vendor Conditions</v-card-title>
          <v-card-text v-for="n in 5" :key="n">
            {{ content }}
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              flat
              color="success"
              @click="showConditions = false"
            >Understood</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </v-layout>
  </v-container>
</template>

<script>
import LineChart from '@/components/lineChart'

export default {
  components: {
    LineChart
  },
  data () {
    return {
      drawer: true,
      // overview data
      period: 'Last Month',
      // datatable headers
      headers: [
        {
          text: 'Order ID',
          value: 'orderId'
        },
        {
          text: 'Item Name',
          value: 'itemName'
        },
        {
          text: 'Quantity',
          value: 'quantity'
        },
        {
          text: 'Client',
          value: 'userName'
        },
        {
          text: 'Destination',
          value: 'destination'
        },
        {
          text: 'Date Ordered',
          value: 'dateOrdered'
        },
        {
          text: 'Date Delivered',
          value: 'dateDelivered'
        },
        {
          text: 'User Verified',
          value: 'userCheck'
        },
        {
          text: 'Retail Price (SGD)',
          value: 'priceSGD' },
        {
          text: 'Amount Collected (SGD)',
          value: 'amountPaid'
        },
        {
          text: 'Discount Applied (%)',
          value: 'discount'
        },
        {
          text: 'Blk.Party Fee (SGD)',
          value: 'fee'
        },
        {
          text: 'Net Sales (SGD)',
          value: 'netSales'
        }
      ],
      // sideView toggles
      fab: false,
      overview: true,
      pies: false,
      profile: false,
      addItem: false,
      updateItem: false,
      // addItem data
      files: [],
      fileIndex: '',
      fileCount: null,
      // addDetails data
      itemName: '',
      price: null,
      description: '',
      itemType: '',
      discount: null,
      listPeriod: 3,
      terms: false,
      showTerms: false,
      showConditions: false,
      viewDetails: false,
      rules: {
        listPeriod: [val => val > 3 || 'Min. 3 days list period'],
        price: [val => val > 0 || 'This field is required. Enter a number higher than zero please.'],
        discount: [val => val >= 0 || 'This field is required. Enter a number equal to or higher than zero please.'],
        name: [val => (val || '').length > 0 || 'This field is required']
      },
      types: [
        'Beer', 'Whisky', 'Vodka', 'Gin', 'Rum', 'Tequila', 'Brandy', 'Wine', 'Champagne'
      ],
      content: `Lorem ipsum dolor sit amet, consectetur adipiscing elit. Integer nec odio. Praesent libero. Sed cursus ante dapibus diam. Sed nisi. Nulla quis sem at nibh elementum imperdiet. Duis sagittis ipsum. Praesent mauris.`,
      // edit items data
      vendorItemDialog: false,
      updatedFile: [],
      itemId: null,
      createDate: null,
      imageURL: '',
      // delete items data
      deleteItemDialog: false
    }
  },
  // vuex actions to retrieve overview data
  mounted () {
    this.$store.dispatch('chartApi', this.vendor)
    this.$store.dispatch('vendorOrdersApi', this.vendor)
    this.$store.dispatch('overallApi', this.vendor)
  },
  computed: {
    vendor () {
      return this.$store.getters.getVendor
    },
    overall () {
      return this.$store.getters.getOverall
    },
    positiveChange () {
      return this.overall.change >= 0
    },
    orders () {
      return this.$store.getters.getVendorOrders
    },
    sales () {
      return this.$store.getters.getSales
    },
    labels () {
      return this.$store.getters.getLabels
    },
    vendorItems () {
      return this.$store.getters.getVendorItems(this.vendor.id)
    },
    // check if all required fields are filled
    addItemFormIsValid () {
      return this.files.length !== 0
    },
    filesLoaded () {
      return this.files.length !== 0
    },
    itemsLoaded () {
      return this.vendorItems.length !== 0
    },
    // get error and success msgs
    error () {
      return this.$store.getters.getError
    },
    success () {
      return this.$store.getters.getSuccess
    },
    loading () {
      return this.$store.getters.getLoading
    },
    detailsFormIsValid () {
      return (
        this.itemName &&
        this.price &&
        this.description &&
        this.itemType &&
        this.listPeriod &&
        this.terms
      )
    },
    updateFormIsValid () {
      return (
        this.itemName &&
        this.price &&
        this.description &&
        this.itemType &&
        this.listPeriod &&
        this.terms
      )
    },
    imageUpdated () {
      return this.updatedFile.length !== 0
    }
  },
  methods: {
    showOverview () {
      // clear all error and success msgs
      this.onDismissed()
      // reload chart, orders and overall apis
      this.$store.dispatch('chartApi', this.vendor)
      this.$store.dispatch('vendorOrdersApi', this.vendor)
      this.$store.dispatch('overallApi', this.vendor)
      // set side view component visibility
      this.overview = true
      this.pies = false
      this.profile = false
      this.addItem = false
      this.updateItem = false
    },
    showPies () {
      // clear all error and success msgs
      this.onDismissed()
      // set side view component visibility
      this.overview = false
      this.pies = true
      this.profile = false
      this.addItem = false
      this.updateItem = false
    },
    showProfile () {
      // clear all error and success msgs
      this.onDismissed()
      // set side view component visibility
      this.overview = false
      this.pies = false
      this.profile = true
      this.addItem = false
      this.updateItem = false
    },
    showAddItem () {
      // clear all error and success msgs
      this.onDismissed()
      // set side view component visibility
      this.overview = false
      this.pies = false
      this.profile = false
      this.addItem = true
      this.updateItem = false
    },
    showUpdateItem () {
      // clear all error and success msgs
      this.onDismissed()
      // get latest items
      this.$store.dispatch('itemsApi')
      // set side view component visibility
      this.overview = false
      this.pies = false
      this.profile = false
      this.addItem = false
      this.updateItem = true
    },
    // addItem detailsForm actions
    // retrieve file details, if any, and render details text fields
    addDetails (index) {
      if (this.files[index].itemName !== null || undefined || '') {
        // set fileIndex to current image index in array
        this.fileIndex = index
        // define detailsForm params
        this.itemName = this.files[index].itemName
        this.price = this.files[index].price
        this.description = this.files[index].description
        this.itemType = this.files[index].itemType
        this.discount = this.files[index].discount
        this.listPeriod = this.files[index].listPeriod
        this.terms = this.files[index].terms
      } else {
        this.resetDetailsForm()
      }
      // show detailsForm
      this.viewDetails = true
    },
    resetDetailsForm () {
      this.itemName = ''
      this.price = null
      this.description = ''
      this.itemType = ''
      this.discount = null
      this.listPeriod = 3
      this.terms = false
    },
    closeDetailsForm () {
      // reset detailsForm
      this.resetDetailsForm()
      // close viewDetails section
      this.viewDetails = false
    },
    submitDetailsForm () {
      // set file details
      this.files[this.fileIndex].itemName = this.itemName
      this.files[this.fileIndex].price = this.price
      this.files[this.fileIndex].description = this.description
      this.files[this.fileIndex].itemType = this.itemType
      this.files[this.fileIndex].discount = this.discount
      this.files[this.fileIndex].listPeriod = this.listPeriod
      this.files[this.fileIndex].terms = this.terms
      // reset addDetails fields
      this.closeDetailsForm()
      // send success alert
      this.$store.dispatch('setSuccess', {message: 'Successfully added ' + this.files[this.fileIndex].itemName + ' details!'})
    },
    // reset addItem form
    resetAddItemForm () {
      this.files = []
    },
    // store selected files in an array
    filesSelected (uploadedFiles) {
      // push files to files array
      for (var i = 0; i < uploadedFiles.length; i++) {
        let filename = uploadedFiles[i].name
        // check for file extension
        if (filename.lastIndexOf('.') <= 0) {
          return this.$store.dispatch('setError', {
            message: 'Please select a valid ' + uploadedFiles[i].name + ' file'
          })
        }
        this.files.push(uploadedFiles[i])
      }
      // generate previews for files
      this.getPreviews()
      // send alert to add item details
      this.$store.dispatch('setSuccess', {
        message: uploadedFiles.length + ' file(s) selected. Please add item details before submitting.'
      })
    },
    getPreviews () {
      // Iterate over all of the files and generate an image preview for each one.
      for (let i = 0; i < this.files.length; i++) {
        // Create a new FileReader object
        const reader = new FileReader()

        // Add an event listener to update src on file preview when the file has been loaded
        reader.addEventListener('load', () => {
          this.$refs['image' + parseInt(i)][0].src = reader.result
        })

        // Read file data through reader and listen to event propagated to set image src to reader result.
        reader.readAsDataURL(this.files[i])
      }
    },
    // remove one file from file array
    removeImage (index) {
      if (index !== -1) {
        this.files.splice(index, 1)
        // close details form
        this.viewDetails = false
      }
      this.getPreviews()
    },
    submitAddItemForm () {
      // set initial ready boolean
      let allReady = true
      // send error alert for any file with undefined itemName
      for (let i = 0; i < this.files.length; i++) {
        if (this.files[i].itemName === undefined) {
          allReady = false
          this.$store.dispatch('setError', {
            message: 'Please enter item details for ' + this.files[i].name + ' before submitting all items'
          })
        }
      }
      // iterate over all files to upload if allReady remains true
      if (allReady) {
        this.fileCount = this.files.length
        for (let j = 0; j < this.files.length; j++) {
          this.upload(this.files[j])
        }
        this.resetAddItemForm()
      }
    },
    upload (file) {
      const payload = {
        file: file,
        itemName: file.itemName,
        priceSGD: file.price,
        description: file.description,
        itemType: file.itemType,
        discount: file.discount,
        listPeriod: file.listPeriod,
        vendorId: this.vendor.id,
        createDate: new Date()
      }
      // dispatch payload to vuex action
      this.$store.dispatch('uploadFile', payload)
    },
    // vendorItem dialog methods
    openDialog (vendorItem) {
      // set dialog form params
      this.imageURL = vendorItem.imageURL
      this.itemName = vendorItem.itemName
      this.price = vendorItem.priceSGD
      this.description = vendorItem.description
      this.itemType = vendorItem.itemType
      this.discount = vendorItem.discount
      this.listPeriod = vendorItem.listPeriod
      this.itemId = vendorItem.itemId
      this.createDate = vendorItem.createDate
      // open itemDialog
      this.vendorItemDialog = true
    },
    cancelUpdate () {
      // reset updatedImage to empty
      this.updatedFile = []
      // close itemDialog
      this.vendorItemDialog = false
    },
    // update item image
    updateFileSelected (file) {
      // push file to updatedFile
      for (var i = 0; i < file.length; i++) {
        let filename = file[i].name
        // check for file extension
        if (filename.lastIndexOf('.') <= 0) {
          return this.$store.dispatch('setError', {
            message: 'Please select a valid ' + filename + ' file'
          })
        }
        this.updatedFile.push(file[i])
      }
      // generate previews for files
      this.getUpdatePreview()
    },
    getUpdatePreview () {
      // Iterate over all of the files and generate an image preview for each one.
      for (let i = 0; i < this.updatedFile.length; i++) {
        // Generate an image preview for updated file.
        const reader = new FileReader()

        // Add an event listener to update src on file preview when the file has been loaded
        reader.addEventListener('load', () => {
          this.$refs['updatedImage'].src = reader.result
        })
        // Read file data through reader and listen to event propagated to set    image src to reader result.
        reader.readAsDataURL(this.updatedFile[i])
      }
    },
    // submit item updates
    submitUpdateForm () {
      const payload = {
        itemId: this.itemId,
        itemName: this.itemName,
        priceSGD: this.price,
        description: this.description,
        itemType: this.itemType,
        discount: this.discount,
        listPeriod: this.listPeriod,
        vendorId: this.vendor.id,
        createDate: this.createDate,
        updateDate: new Date()
      }
      if (this.updatedFile.length !== 0) {
        payload.updatedFile = this.updatedFile[0]
      } else {
        payload.updatedFile = null
        payload.imageURL = this.imageURL
      }
      // dispatch payload to vuex action
      this.$store.dispatch('updateFile', payload)
      // get latest items
      this.$store.dispatch('itemsApi')
    },
    // open confirm delete item dialog
    removeItem (vendorItem) {
      // set dialog params
      this.itemId = vendorItem.itemId
      this.imageURL = vendorItem.imageURL
      this.itemName = vendorItem.itemName
      this.price = vendorItem.priceSGD
      // open deleteItemDialog
      this.deleteItemDialog = true
    },
    // delete item after confirmation
    deleteItem () {
      // delete item from firebase
      this.$store.dispatch('deleteItem', {
        itemId: this.itemId
      })
      // get latest items
      this.$store.dispatch('itemsApi')
      // close delete item dialog
      this.deleteItemDialog = false
    },
    onDismissed () {
      this.$store.dispatch('clearError')
      this.$store.dispatch('clearSuccess')
    }
  }
}
</script>

<style scoped>
  /* style for file dropbox */
  .dropbox {
    border: 2px dashed grey;
    background: lightcyan;
    color: dimgray;
    padding: 10px 10px;
    min-height: 300px; 
    position: relative;
    cursor: pointer;
  }

  .dropbox:hover {
    background: lightblue; 
  }

  .dropbox p {
    font-size: 1.2em;
    text-align: center;
    padding: 50px 0;
  }
  /* hide input field */

  .input_file {
    opacity: 0;
    width: 100%;
    height: 300px;
    position: absolute;
    cursor: pointer;
  }

  .input_updateFile {
    opacity: 0; 
    top: 50%;
    width: 100%;
    height: 30px;
    position: absolute;
    cursor: pointer;
  }
  /* style for netflix like gallery scroll */
  .gallery-row > .tile-scroll{
    overflow-x: auto;
    white-space: nowrap;
  }

  .gallery-row > .tile-scroll > .tile {
    display: inline-block;
    float: none;
  }

  .tile {
    padding-bottom: 0px;
    padding-top: 0px;
    display: block;
    width: 45%;
    height: auto;
    position: relative;
  }

  .tile__img {
    object-fit: cover;
    width: 100%;
    height: 300px;
  }

  .updateTile__img {
    object-fit: cover;
    width: 100%;
    height: 500px;
  }
  /* style for gallery tile overlay*/
  .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #545454 !important;
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: height 0.5s ease;
    opacity: .85;
  }

  .tile:hover .overlay {
    height: 100%;
  }

  .tile:hover {
    transition: all .2s ease-in-out;
    transform: scale(1.02); 
    position:relative;
    transition-timing-function:cubic-bezier(0.5, 0, 0.1, 1);
    transition-duration: 400ms;
  }
  /* style for texts in overlay */
  .remove {
    white-space: nowrap;
    color: #FFFFF8;
    font-size: 20px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 33%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
  }

  .remove:hover {
    cursor: pointer;    
    color: #E64F64;
  }

  .details {
    white-space: nowrap;
    color: #FFFFF8;
    font-size: 20px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 66%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    cursor: pointer;    
  }

  .changeImage {
    white-space: nowrap;
    color: #FFFFF8;
    font-size: 20px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    cursor: pointer;    
  }
  
  .sideView {
    overflow-y: scroll;
  }

</style>




